@charset "UTF-8";
/*===================layout===================*/
@import url("//fonts.googleapis.com/css?family=Noto+Sans+TC:400,500,700|Roboto:400,500,700&display=swap");
* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", "Noto Sans TC", Arial, Verdana, Geneva, sans-serif;
  color: #252525;
  background-color: #ffffff;
  line-height: 28px;
  font-size: 16px;
  position: relative;
  letter-spacing: 0.05em;
  -webkit-text-size-adjust: none;
}

.body-hidden {
  height: 100%;
  overflow: hidden;
}

a {
  color: #252525;
  text-decoration: none;
  transition: all .3s ease-out;
}

a:hover {
  color: #940518;
  text-decoration: none;
  transition: all .3s ease-out;
}

.wrapper {
  max-width: 1600px;
  margin: 0 auto;
}

.wrapper--fluid {
  margin: 0;
  max-width: 100%;
}

@media only screen and (max-width: 1630px) {
  .wrapper {
    padding: 0 15px;
  }
}

.hide {
  display: none;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.edit img, .edit iframe {
  max-width: 100%;
  height: auto;
}

.edit table {
  width: 100%;
  max-width: 100%;
}

/*IE 滿版背景圖晃動 */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  html {
    overflow: hidden;
    height: 100%;
  }
  body {
    overflow: auto;
    height: 100%;
  }
}

.form-wrapper {
  padding: 50px 0;
}

@media only screen and (max-width: 768px) {
  .form-wrapper {
    padding: 50px 30px;
  }
}

@media only screen and (max-width: 480px) {
  .form-wrapper {
    padding: 50px 10px;
  }
}
