//This is sass file
/*===================layout===================*/
@import url('//fonts.googleapis.com/css?family=Noto+Sans+TC:400,500,700|Roboto:400,500,700&display=swap');
* {
	box-sizing: border-box; }
body {
	font-family: $font;
	color: $textColor;
	background-color: $mainColor1;
	line-height: $fontLh;
	font-size: $fontSz;
	position: relative;
	letter-spacing: 0.05em;
	-webkit-text-size-adjust: none; }
.body-hidden {
	height: 100%;
	overflow: hidden; }
a {
	color: $textColor;
	text-decoration: none;
	transition: all .3s ease-out;
	&:hover {
		color: $textHColor;
		text-decoration: none;
		transition: all .3s ease-out; } }
.wrapper {
	max-width: $wrapWidth * 1px;
	margin: 0 auto;
	//滿版寬度
	&--fluid {
		margin: 0;
		max-width: 100%; }
	@include rwd($gridXL + 30 + 'px') {
		padding: 0 15px; } }
.hide {
	display: none; }
.flex {
	display: flex;
	flex-wrap: wrap; }
.edit {
	img,iframe {
		max-width: 100%;
		height: auto; }
	table {
		width: 100%;
		max-width: 100%; } }

/*IE 滿版背景圖晃動 */
@media screen and (-ms-high-contrast: active),(-ms-high-contrast: none) {
	html {
		overflow: hidden;
		height: 100%; }
	body {
		overflow: auto;
		height: 100%; } }
